<template>
    <div id="wrapper" class="home">
        <div class="header">
            <div class="menu">
                <div v-on:click="menu">&#9776;</div>
                <div class="hNotice">{{delivery}}</div>
            </div>
        </div>
        <div class="visual" name="fade">
            <img v-bind:src="baseUrl+vNotice[currentNum % vNotice.length].imgurl" v-bind:alt="vNotice[0].title">
        </div>
        <div class="contentWrap">
            <div class="lineFirst">
                <div class="trig">
                    <ul id="categories" style="left:-15%">
                        <li>
                            <div class="voiceOrder" v-on:click="routing('unimplemented')">
                            </div>
                        </li>
                    </ul>
                </div>

                <div class="trig">
                    <ul id="categories" style="left:-5%;">
                        <li>
                            <div class="order"  v-on:click="routing('order')">
                            </div>
                        </li>
                    </ul>
                </div>

                <div class="trig ">
                    <ul id="categories" style="right:6.5%">
                        <li>
                            <div class="advertise" v-on:click="routing('unimplemented')">
                            </div>
                        </li>
                    </ul>
                </div>
            </div>
            
            <div class="lineSecond">
                <div class="trig" style="position:absolute">
                    <ul id="categories" style="left:-122%;">
                            <li>
                                <div style="background:rgba(108,138,55)"  v-on:click="routing('not')">
                                </div>
                            </li>
                        </ul>
                </div>
                
                <div class="trig ">
                    <ul id="categories" style="left:-10%;">
                        <li>
                            <div class="recentlyOrder"  v-on:click="routing('orderHistory')">
                            </div>
                        </li>
                    </ul>
                </div>
            

            
                <div class="trig">
                    <ul id="categories" style="left:-1%">
                        <li>
                            <div class="favorite"  v-on:click="routing('unimplemented')">
                            </div>
                        </li>
                    </ul> 
                </div>
                
                <div class="trig" style="position:absolute">
                    <ul id="categories" style="right:-2%;">
                            <li>
                                <div style="background:rgba(131,39,32)"  v-on:click="routing('not')">
                                </div>
                            </li>
                        </ul>
                </div>
            </div>

            <div class = "lineThird">
                <div class="trig">
                    <ul id="categories" style="left:-15%">
                        <li>
                            <div class="refundHistory"  v-on:click="routing('refundHistory')">
                            </div>
                        </li>
                    </ul> 
                </div>

                <div class="trig ">
                    <ul id="categories" style="left:-5%">
                        <li>
                            <div class="statistics"  v-on:click="routing('statistics')">
                            </div>
                        </li>
                    </ul> 
                </div>

                <div class="trig ">
                    <ul id="categories" style="right:6.5%">
                        <li>
                            <div class="settings"  v-on:click="routing('settings')">
                            </div>
                        </li>
                    </ul> 
                </div>
            </div>
        </div>
        <div class="content">
        </div>
        <div class="footer">
            <div class="fNotice">
                <div class="noticeHeader">
                    <div class="noticeTitle">
                        <h2>공지사항</h2>
                    </div>
                    <div class="noticeMore">
                        더보기 >
                    </div>
                </div>
                <div class="noticeContent" v-for="(item, index) in fNotice" v-bind:key="index">
                    <div class="noticeCon">
                        {{item.title}}
                    </div>
                    <div class="noticeDate">
                        {{item.regDate}}
                    </div>
                </div>
            </div>
            <div class="copy">
                <div class="info">
                    사업자정보확인 |
                </div>
                <div class="terms">
                    이용약관 |
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Main',
        data () {
            return{
                user: '',
                deliveryName: '',
                baseUrl: 'http://freshntech.cafe24.com',
                hNotice: [],
                vNotice: [],
                fNotice: [],
                currentNum:0,
                timer:null,
            }
        },
        methods: {
            menu () {
                alert('menu');
            },
            startRotation () {
                this.timer = setInterval(this.next, 3000);
            },
            next () {
                this.currentNum +=1;
            },
            routing(route){
                this.$router.replace(route);
            }
        },
        created() {
            console.log(this.$store.state);
            if(!this.$store.state.isAuth){
                this.$router.replace('/login');
                return;
            }else{
                this.user = this.$store.state.user;
                this.delivery = this.$store.state.delivery;
                this.hNotice = this.$store.state.hNotice;
                this.vNotice = this.$store.state.vNotice;
                this.fNotice = this.$store.state.fNotice;
                this.startRotation();
            }
        }
    }
</script>

<style>
    .home{
        overflow:hidden;
    }
    .home .header{
        width: 100%;
        height: 5%;
        padding: 0 3%;
        line-height: 250%;
    }
    .home .hNotice{
        width: 30%;
        background-image: url("../assets/Under_Color.png");
        background-repeat: no-repeat;
        background-size: 20%;
        background-position: center right;
    }
    .home .menu > div{
        display: inline-block;
        font-size: 1.3em;
    }
    .home .visual{
        width: 100%;
        height: 25%;
    }
    .home .visual > img {
        width: 100%;
        height: 100%;
    }
    .home .content{
        width: 100%;
        height:45%;
    }
    .home  .footer{
        width: 100%;
        height: 25%;
    }
    .home .fNotice{
        width: 90%;
        height: 80%;
        margin:0 auto;
    }
    .home .copy{
        width: 90%;
        margin:0 auto;
        height: 20%;
    }
    .home .noticeHeader{
        width: 90%;
        height: 25%;
        padding: 1% 3%;
        border-bottom: 0.1px solid rgba(236,236,236);
    }
    .home .noticeContent{
        width: 90%;
        padding: 0% 3%;
        border-bottom: 0.1px solid rgba(236,236,236);
    }
    .home .noticeTitle{
        width: 70%;
        height: 100%;
        display: inline-block;
        font-weight: bold;
    }
    .home .noticeCon{
        width: 70%;
        height:100%;
        font-weight: bold;
        display: inline-block;
    }
    .home .noticeMore{
        width: 25%;
        height: 100%;
        display:inline-block;
        color: gray;
        text-align: right;
        font-size: 0.8em;
    }
    .home .noticeDate{
       width: 25%;
        height: 100%;
        display:inline-block;
        text-align: right;
        color: gray; 
    }
    .home .copy > div{
        display:inline-block;
        color: gray;
        font-size: 0.8em;
    }
    .home .fade-enter-active, .fade-leave-active {
        transition: all 0.8s ease;
        overflow: hidden;
        visibility: visible;
        opacity: 1;
        position: absolute;
    }
    .home .fade-enter, .fade-leave-to {
        opacity: 0;
        visibility: hidden;
    }
    .lineFirst, .lineSecond, .lineThird{
       position:relative;
    }
    
</style>

<style>
.home .contentWrap{
    width:100%;
    height:55%;
    margin: 0 auto;
    position:absolute;
}
.home .contentWrap > div{
    display: inline-block;
    width: 100%;
    height: 33%;
    text-align: center;
}
.home .trig{
    display: inline-block;
    position: relative;
    width: 30%;
    height: 100%;
}
.home .voiceOrder{
    background-image: url('../assets/VoiceOrder.png');
    background-color: rgba(28, 151, 122);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;        
}
.home .order{
    background-image: url('../assets/Order.png');
    background-color: rgba(240,144,35);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;        
}
.home .advertise{
    background-image: url('../assets/Marketing.png');
    background-color: rgba(181,50,42);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;        
}.home .recentlyOrder{
    background-image: url('../assets/Recently.png');
    background-color: rgba(22,110,89);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;        
}
.home .favorite{
    background-image: url('../assets/Favorite.png');
    background-color: rgba(190,107,14);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;        
}
.home .refundHistory{
    background-image: url('../assets/Refund.png');
    background-color: rgba(28,151,122);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;        
}
.home .statistics{
    background-image: url('../assets/Statistics.png');
    background-color: rgba(240,144,35);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;        
}
.home .settings{
    background-image: url('../assets/Setting.png');
    background-color: rgba(181,50,42);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center center;     
}
.lineFirst #categories{
  width:90%;
  top:-25%;
  position: absolute;
}
.lineSecond #categories{
  width:90%;
  top: -50%;
  position: absolute;
}
.lineThird #categories{
    width:90%;
    top: -75%;
    position: absolute; 
}
#categories li{
  position: relative;;
  list-style-type:none;
  width:112%; /* = (100-2.5) / 3.5 */
  padding-bottom: 129%; /* =  width /0.866 */
  overflow:hidden;
  transform: rotate(-60deg) skewY(30deg);
}
#categories li:nth-child(3n+2){
  margin:0 1%;
}
#categories li:nth-child(6n+4){
  margin-left:0.5%;
}
#categories li:nth-child(6n+4), #categories li:nth-child(6n+5), #categories li:nth-child(6n+6) {
margin-top: -6.9285714285%;
margin-bottom: -6.9285714285%;
transform: translateX(50%) rotate(-60deg) skewY(30deg);
}
#categories li *{
  position:absolute;
  visibility:visible;
}
#categories li > div{
  width:100%;
  height:100%;
  text-align:center;
  color:#fff;
  overflow:hidden;
  transform: skewY(-30deg) rotate(60deg);
}

/* HEX CONTENT */
#categories li img{
  left:-100%; right:-100%;
  width: auto; height:100%;
  margin:0 auto;
}

#categories div h1, #categories div p{
  width:100%;
  background-color:#008080; background-color: rgba(0, 128, 128, 0.8);
  transition: top .2s ease-out, bottom .2s ease-out, .2s padding .2s ease-out;
}
#categories li h1{
  bottom:110%;
  font-style:italic;
  font-weight:normal;
  font-size:1.5em;
  padding-top:100%;
  padding-bottom:100%;
}
</style>



