<template>
    <div id="wrapper" class="orderHistory">
        <div class="orderDetail active" v-if="display">
            <div class="table">
                <div class="header">
                    <div class="cancel" v-on:click="closeModal()">
                        x
                    </div>
                    <div class="htitle">
                        주문상세
                    </div>
                    <div class="detailInfo">
                        {{detailId}}
                        <div v-for="(item, index) in orderDetail" v-bind:key="index">
                            {{item.item.itemName}}
                            {{item.orderitem.qty}}
                            {{item.orderitem.price}}
                            {{item.orderitem.amount}}
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="header">
            <div class="prev" v-on:click="cancel"></div>
            <div class="htitle">주문내역</div>
        </div>
        <div class="condition">
            <select class="filterDate" v-model="lastDay">
                <option value="7">지난 7일</option>
                <option value="30">지난 한달</option>
                <option value="365">지난 1년</option>
            </select>
            <select class="filterState" v-model="orderState" v-on:change="filterHistory">
                <option value="전체">전체상태</option>
                <option value="배송예정">배송예정</option>
                <option value="배송중">배송중</option>
                <option value="배송완료">배송완료</option>
                <option value="주문접수">주문접수</option>
                <option value="주문취소">주문취소</option>
                <option value="출고거절">출고거절</option>
                <option value="결제대기">결제대기</option>
            </select>
        </div>
        <div class="historyList">
            <div class="historyItem" v-for="(item, index) in displayItem" v-bind:key="index" v-on:click="callModal(item.id)">
                <div class="orderInfo">
                    <span>{{item.orderdate}}</span> <br>
                    <span>주문금액 {{numberWithCommas(item.amount)}}</span>
                </div>
                <div class="orderState">
                    {{item.orderstate}}
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    var baseurl = "http://freshntech.cafe24.com";
    export default {
        name:'OrderHistory',
        data () {
            return {
                lastDay: '7',
                orderState: '전체',
                historyItem: [],
                displayItem: [],
                detailId:'',
                orderDetail: [],
                display: false,
                styleColor: [{"배송예정": 'black'}, {"배송중":""}, {"배송완료":"gray"}, {"출고거절":"red"}, {"주문취소":"lightgray"}],
            }
        },
        methods: {
            auth(){
                if(!this.$store.state.isAuth){
                    this.$router.replace('/login');
                }
            },
            getHistory () {
                this.$http.get(baseurl + '/order/getOrder/' + this.$store.state.usercode + '?condition=' + this.lastDay)
                .then((response) => {
                    this.historyItem = response.data;
                    this.filterHistory();
                })
                .catch((err) => {
                    console.log(err);
                })
            },
            filterHistory () {
                if(this.historyItem.length <= 0){
                    return;
                }
                if(this.orderState ==="전체"){
                    this.displayItem = this.historyItem;
                    return;
                }
                let history;
                for(var i=0; i<this.historyItem.length; i++){
                    history = this.historyItem.filter((item) => {
                        return item.orderstate === this.orderState;
                    })
                }
                this.displayItem = history;
            },
            cancel() {
                this.$router.replace('/');
            },
            numberWithCommas(x) {
                return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            },
            callModal(id) {
                this.detailId = id;
                this.$http.get(baseurl + '/orderdetail/' + id)
                .then((response) => {
                    this.orderDetail = response.data;
                    this.display=true;
                })
                .catch((err) => {
                    console.log(err)
                });
            },
            closeModal() {
                this.display=false;
                this.orderDetail = undefined;
            }
        },
        created(){
            this.auth();
            this.getHistory();
            console.log(this.styleColor)
        }
    }
</script>

<style>
    .orderHistory {
        background: rgba(248,248,248);
    }
    .orderHistory .header{
        width:100%;
        height:5%;
        color:white;
        background-image: url('../assets/HeaderBar.png');
        background-repeat: no-repeat;
        background-size: cover;
    }
    .orderHistory .header .prev{
        width:10%;
        height:100%;
        float: left;
        background-image: url('../assets/Prev.png');
        background-size:contain;
        background-position: center center;
        background-repeat: no-repeat;
    }
    .orderHistory .header .htitle{
        width: 90%;
        height:100%;
        font-weight: bold;
        font-size: 1.5em;
        text-align: center;
        padding-top:1%;
    }
    .orderHistory .condition{
        width: 95%;
        height: 5%;
        margin:2.5% auto;
        text-align: center;
    }
    .orderHistory .condition .filterDate{
        width:45%;
        height:50%;
        background: #fff;
    }
    .orderHistory .condition .filterState{
        width:45%;
        height:50%;
        background: #fff;
    }
    .orderHistory .historyList{
        width:100%;
        height:87%;
        overflow: auto;
    }
    .orderHistory .historyItem{
        width:100%;
        height:8%;
        background: #fff;
        margin: 1% 0;
    }
    .orderHistory .historyItem .orderInfo{
        width:70%;
        height: 100%;
        display:inline-block;
        padding-top:2%;
    }
    .orderHistory .historyItem .orderState{
        width: 25%;
        height:100%;
        display:inline-block;
        text-align: right;
    }
</style>

<style>
    .orderDetail{
        position: absolute;
        background: rgba(0,0,0,0.4);
        width:100%;
        height:100%;
    }
    .inactive{
        display:none;
    }
    .active{
        display:block;
    }
    .orderDetail .table{
        width: 80%;
        height: 60%;
        background: white;
        margin: 40% auto;
    }
    .orderDetail .header{
        width:100%;
        height:5%;
        color:white;
        background-image: url('../assets/HeaderBar.png');
        background-repeat: no-repeat;
        background-size: cover;
    }
    .orderDetail .header .cancel{
        width:10%;
        height:100%;
        float: left;
        text-align: center;
        font-size: 1.2em;
        font-weight: bold;
    }
    .orderDetail .header .htitle{
        width: 90%;
        height:100%;
        font-size: 0.9em;
        font-weight: bold;
        text-align: center;
    }
    .orderDetail .detailInfo{
        color: black;
    }
</style>



