<template>
    <div id="wrapper" class="order">
        <div class="header">
            <div class="prev" v-on:click="cancel"></div>
            <div class="htitle">주문화면</div>
        </div>
        <div class="orderList">
            <div class="orderHeader">
                <h3>주문상품</h3>
                <router-link to="addGoods">
                    <div>상품 추가</div>
                </router-link>
            </div>
            <div class="orderItemList">
                <div class="orderItem" v-for="(item, index) in orderList " v-bind:key = "item.id">
                    <div class="itemSignature">
                        <span class="itemTitle">[{{item.manufacturer}}] {{item.itemName}}</span><br>
                        <span class="unit">{{item.unit}}</span> | <span class="origin">{{item.origin}}</span><br>
                        <span class="qty">
                            <button v-on:click="increaseQty(index)">+</button>
                            <span>{{item.qty}}</span>
                            <button v-on:click="decreaseQty(index)">-</button>
                        </span>
                    </div>
                    <div class="itemCal">
                        <span class="remove">x</span><br>
                        <span class="price">{{numberWithCommas(item.price1)}}</span><br>
                        <span class="itemTotal">{{numberWithCommas(item.amount)}}원</span>
                    </div>
                </div>
            </div>
            <div class="amount">
                <div class="amountTitle">
                    <div>총 주문수량</div>
                    <div>총 상품금액</div>
                </div>
                <div class="amountPrice">
                    <div>{{numberWithCommas(totalQuantity)}}개</div>
                    <div>{{numberWithCommas(totalPrice)}}원</div>
                </div>
            </div>
        </div>
        <div class="deliveryDate">
            <div class="dateTitle">
                <div><span style="font-weight:bold">배송요청일</span><br><span style="color:rgba(43,154,131)">{{deliveryDate}}</span></div>
                <button>
                    날짜변경
                </button>
            </div>
        </div>
        <div class="deliveryLocation">
            <div class="locate">
                <span style="font-weight:bold;">배송지</span><br><span style="font-size:0.8em;color:gray;">{{deliveryLocation}}</span>
            </div>
        </div>
        <div class="delRequest">
            <div style="width:100%;font-weight:bold">요청사항<br><input type="text" v-model="delRequest" placeholder="배송 및 상품에 대한 추가 요구사항 입력"></div>
        </div>
        <div class="orderPayment">
            <div style="width:100%;height:100%;position:relative">
                <span style="font-weight:bold;">결제수단 선택</span><br>
                <input checked type="radio" name="payMethod" id="card" value="신용카드" v-model="payMethod" > <label for="card">신용카드</label><br>
                <input type="radio" name="payMethod" id="account" value="계좌이체" v-model="payMethod"> <label for="account">계좌이체</label>
            </div>
        </div>
        <div class="reqOrder">
            <div>
                <h2 style="float:left;width:50%;padding-left:2.5%">총주문금액</h2>
                <h2 style="float:right;width:30%;text-align:right; padding-right:2.5%;color:rgba(43,154,131)">{{numberWithCommas(totalPrice)}}원</h2>
            </div>
           
           <div class="buttons">
            <button class="cancel" v-on:click="cancel">주문취소</button>
            <button class="submit" v-on:click="order">주문하기</button>
           </div>
        </div>
    </div>
    
</template>

<script>
    let d = new Date();
    var baseurl = "http://freshntech.cafe24.com";
    export default {
        name: 'Order',
        data () {
            return {
                orderList: null,
                user: '',
                delRequest: '',
                payMethod: '신용카드',
                deliveryLocation: '',
                deliveryDate: d.getFullYear() + '-' + (d.getMonth() + 1) + '-' + d.getDate()
            }
        },
        methods: {
            auth() {
                if(!this.$store.state.isAuth){
                    this.$router.replace('/login');
                }else{
                    console.log(typeof this.$store.state.orderList)
                    if(typeof this.$store.state.orderList === 'undefined'){
                        this.$http.get(baseurl + '/customeritem/' +  this.$store.state.usercode)
                        .then((response) => {
                            console.log(response.data);
                            this.$store.state.orderList = response.data.customerItemList;
                            this.$store.state.deliveryLocation = response.data.userAddress;
                            this.deliveryLocation = this.$store.state.deliveryLocation;
                            this.user = this.$store.state.user;
                            for(var i = 0; i < response.data.customerItemList.length; i++){
                                this.$store.state.orderList[i].qty = 1;
                                this.$store.state.orderList[i].amount = this.$store.state.orderList[i].qty * this.$store.state.orderList[i].price1;
                            }
                            this.orderList = this.$store.state.orderList;
                            //console.log(this.$store.state.orderList);
                        })
                        .catch((err) => {
                            console.log(err)
                        })
                    }else{
                        for(var i = 0; i< this.$store.state.orderList.length; i++){
                            this.$store.state.orderList[i].qty = 1;
                            this.$store.state.orderList[i].amount = this.$store.state.orderList[i].qty * this.$store.state.orderList[i].price1;
                        }
                        this.orderList = this.$store.state.orderList;
                        this.deliveryLocation = this.$store.state.deliveryLocation;
                        
                    }
                }
            },
            increaseQty(idx) {
                console.log('active')
                if(this.orderList[idx].qty + 1 >= this.orderList[idx].itemQty){
                    return;
                }
                this.orderList[idx].qty++;
                this.orderList[idx].amount = this.orderList[idx].qty * this.orderList[idx].price1;
            },
            decreaseQty(idx) {
                console.log('active')
                if(this.orderList[idx].qty -1 <= 0){
                    return;
                }
                this.orderList[idx].qty--;
                this.orderList[idx].amount = this.orderList[idx].qty * this.orderList[idx].price1;
            },
            numberWithCommas(x) {
                return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
            },
            cancel () {
                this.$store.state.orderList = undefined;
                this.$router.replace('/');
            },
            order () {
                var value = {
                    orderItemList: this.orderList,
                    userId: this.$store.state.usercode,
                    reqDate: this.deliveryDate,
                    amount: this.totalPrice,
                    points: 0,
                    payMethod: this.payMethod,
                    requests: this.delRequest 
                }
                console.log(value);
                this.$http.post(baseurl + '/order/insert', value)
                .then((response) => {
                    console.log(response);
                    if(response.data === true){
                        console.log('ok')
                        this.$store.state.orderList=undefined;
                        this.$router.replace('/');
                        return;
                    }
                })
                .catch((err) => {
                    console.log(err)
                })
            },
            calPrice () {
                for(var i=0; i< this.orderList.length; i++){
                    this.totalPrice +=this.orderList[i].price * this.orderList[i].qty;
                }
            },
            calQty () {
                for(var i=0; i<this.orderList.length; i++){
                    this.totalQuantity += this.totalQuantity[i].qty;
                }
            }
        },
        mounted() {
            this.auth();
        },
        computed: {
            totalPrice () {
                return this.orderList.reduce((sum, item) => {
                    return sum + (item.amount)
                }, 0);
            },
            totalQuantity () {
                return this.orderList.reduce((sum, item) => {
                    return sum + item.qty
                }, 0);
            }
        }
    }
    
</script>

<style>
    .order{
        width: 100%;
        height: 100%;
        overflow: hidden;
        background: rgba(248,248,248);
    }
    .order .header{
        width:100%;
        height:5%;
        color:white;
        background-image: url('../assets/HeaderBar.png');
        background-repeat: no-repeat;
        background-size: cover;
    }
    .order .header .prev{
        width:10%;
        height:100%;
        float: left;
        background-image: url('../assets/Prev.png');
        background-size:contain;
        background-position: center center;
        background-repeat: no-repeat;
    }
    .order .header .htitle{
        width: 90%;
        height:100%;
        font-weight: bold;
        font-size: 1.5em;
        text-align: center;
    }
    .order .orderList{
        width: 100%;
        height: 45%;
        background: #ffffff;
    }
    .order .orderHeader{
        width: 100%;
        height: 15%;
        line-height: 300%;
        border-bottom: 1px solid rgba(243,242,243);
    }
    .order .orderHeader h3{
        width: 44%;
        height: 100%;
        padding: 0 0 0 5%;
        display: inline-block;
    }
    .order .orderHeader a{
        width: 49%;
        height: 100%;
        display: inline-block;
        text-align: right;
    }
    .order .orderHeader a:link, .order .orderHeader a:visited{
        color: rgba(0,153,118)
    }
    .order .orderHeader div{
        background-image: url("../assets/Add.png");
        background-repeat: no-repeat;
        background-size: 12%;
        background-position: 67% center;
    }
    .order .orderItemList {
        width: 100%;
        height: 65%;
        overflow: auto;
        border-bottom: 0.1px solid rgba(243,242,243);
    }
    .order .orderItem{
        width: 95%;
        height: 40%;
        margin: 0 auto;
        border-bottom: 0.1px solid rgba(243,242,243);
    }
    .order .orderItem .itemTitle{
        font-weight: bold;
    }
    .order .orderItem .itemSignature{
        width: 70%;
        height: 100%;
        display: inline-block;
    }
    .order .orderItem .itemCal{
        width: 25%;
        height: 100%;
        display: inline-block;
        text-align: right;
    }
    .order .orderItem .remove,.order .orderItem .itemTotal{
        font-weight: bold;
        font-size: 1.1em;
    }
    .order .orderItem .price{
        font-size: 0.8em;
    } 
    .order .orderItem .qty{
        display: inline-block;
        background: white;
        width: 30%;
        height: 25%;
    }
    .order .orderItem .qty span{
        width: auto;
        height: 100%;
    }
    .order .orderItem .qty button{
       width: 30%;
       height: 100%;
       background: rgba(223, 223, 223);
       border-radius: 5px;
    }
    .order .amount{
        width: 95%;
        height: 10%;
        font-size: 1.2em;
        margin: 2% auto;
    }
    .order .amount .amountTitle {
        display: inline-block;
        width: 45%;
    }
    .order .amount .amountPrice {
        display: inline-block;
        width: 45%;
        text-align: right;
    }
    .order .deliveryDate{
        width: 100%;
        height: 8%;
        background: white;
        margin: 2% auto;
    }
    .order .deliveryDate .dateTitle{
        width: 100%;
        height: 100%;
        margin-left: 2.5%;
    }
    .order .deliveryDate .dateTitle div{
        float: left;
    }
    .order .deliveryDate button {
        float:right;
        margin-right: 7%;
        width: 25%;
        height: 100%;
        color:white;
        font-weight: bold;
        background: rgba(152,152,152);
    }
    .order .deliveryLocation{
        width: 100%;
        height: 8%;
        background: white;
        display:flex;
        align-items: center;
        padding-left: 2.5%;
    }
    .order .delRequest{
        width: 100%;
        height: 8%;
        margin-top: 2.5%;
        background: white;
        display:flex;
        align-items: center;
        padding-left: 2.5%;
    }
    .order .delRequest input{
        width: 100%;
        font-size: 1.1em;
    }
    .order .orderPayment{
        width: 100%;
        height: 9%;
        background: white;
        display:flex;
        align-items: center;
        justify-content: center;
        margin: 2.5% 0;
        padding-left: 2.5%;
        background: white;
    }
    .order .orderPayment input[type='radio']{
        display:none;
    }
    .order .orderPayment input[type='radio'] + label::before{
        content:'';
        display: inline-block;
        width: 5%;
        height: 30%;
        vertical-align: middle;
        margin: -1% 1% 0 0;
        background-image: url("../assets/CheckBox_Unactive.png");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
    }
    .order .orderPayment input[type='radio']:checked + label::before{
        background-image: url("../assets/CheckBox_Active.png");
        background-size: contain;
        background-repeat: no-repeat;
        background-position: center center;
    }
    .order .reqOrder{
        width: 100%;
        height: 11%;
        background: white;
    }
    .order .reqOrder .buttons{
        width: 100%;
        height: 50%;
        text-align: center;
    }
    .order .reqOrder .cancel{
        width: 40%;
        height: 100%;
        background: rgba(248,248,248);
        border: 0.1px solid gray;
        font-weight: bold;
    }
    .order .reqOrder .submit{
        width: 40%;
        height: 100%;
        background: rgba(29,151,122);
        color:white;
        font-weight: bold;
    }
</style>



