import Vue from 'vue'
import Router from 'vue-router'
import {
  Main,
  Loading,
  Login,
  Order,
  AddGoods,
  OrderHistory,
  RefundHistory,
  Statistics,
  Settings,
  Test
} from '../components';


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/test',
      name: 'Test',
      component: Test
    },
    {
      path: '/',
      name: 'Main',
      component: Main,
      props: true
    },
    {
      path: '/login',
      name: 'Login',
      component: Login,
    },
    {
      path: '/order',
      name: 'Order',
      component: Order,
      props: true
    },
    {
      path: '/addGoods',
      name: 'AddGoods',
      component: AddGoods,
      props: true
    }, 
    {
      path: '/orderHistory',
      name: 'OrderHistory',
      component: OrderHistory,
      props: true
    },
    {
      path: '/refundHistory',
      name: 'RefundHistory',
      component: RefundHistory,
      props: true
    },
    {
      path: '/statistics',
      name: 'Statistics',
      component: Statistics,
      props: true
    },
    {
      path: '/settings',
      name: 'Settings',
      component: Settings,
      props: true
    },
  ]
})



