<template>
    <div id="wrapper" class="login">
        <div class="lBox">
            <p>
                <input v-model="userId" type="text" height="100" placeholder="거래처 아이디">
            </p>
            <p>
                <input v-model="userPw" type="password" placeholder="비밀번호">
            </p>
            <p>
                <button v-on:click="reqLogin()">로 그 인</button>
            </p>
        </div>
        <div class="member">
            회원가입 | 비밀번호찾기
        </div>
    </div>
</template>

<script>
    var baseurl = "http://freshntech.cafe24.com";
    export default {
        name: 'Login',
        data () {
            return {
                userId: '',
                userPw: ''
            }
        },
        methods: {
            reqLogin() {
                var  value = {
                    userid: this.userId,
                    userpw: this.userPw
                }
                this.$http.post(baseurl + '/customer/login', value)
                .then((response) => {
                    console.log(response.data);
                    if(response.status === 200 ){
                        console.log(response.data.customer)
                        this.$store.state.delivery = response.data.customer.deliverer_bname;
                        this.$store.state.userid = response.data.customer.userid;
                        this.$store.state.usercode = response.data.customer.id;
                        this.$store.state.user = response.data.customer.bname;
                        this.$store.state.vNotice = response.data.adBoard;
                        this.$store.state.fNotice = response.data.customerNotice;
                        this.$store.state.isAuth = response.data.customer.isAuth;
                        this.$router.push('/');
                        return;
                    }
                })
                .catch((error) => {
                    console.log(error);
                    alert('경고');
                    this.userid = "";
                    this.userpw = ""
                    return;
                })
            },
            auth() {
                if(this.$store.state.isAuth){
                    console.log('exit')
                    window.close();
                    //window.open('about:blank','_self').self.close();  // IE에서 묻지 않고 창 닫기
                }
            },
        },
        mounted(){
            this.auth()
        }
    }
</script>

<style>
    .login{
        text-align: center;
        display: flex;
        justify-content: center;
        background-color: #000;
        background-image: url('../assets/logo.png');
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center 30%;
    }
    .lBox{
        width: 90%;
        height: 25%;
        bottom: 10%;
        position: fixed;
        vertical-align: middle;
        background: #fff;
    }
    .lBox p{
        width: 100%;
        height: 30%;
        margin: 2% auto;
        display:flex;
        justify-content: center;
        line-height: 30%;
    }
    .lBox p:nth-child(1) {
        background-image: url("../assets/Login_User.png");
        background-repeat: no-repeat;
        background-size: 10%;
    }
    .lBox p:nth-child(2) {
        background-image: url("../assets/Login_Password.png");
        background-repeat: no-repeat;
        background-size: 10%;
    }
    .lBox p input[type="text"], .lBox p input[type="password"]{
        padding: 0 5%;
        width: 70%;
        height: 70%;
        border: none;
        border-bottom: 1px solid #000;
    }
    .lBox p button{
        width: 80%;
        height: 90%;
        border: none;
        /* background-image: linear-gradient(to right,#38ef7d, #11998e); */
        background-image: url('../assets/Btn.png');
        background-size: contain;
        color: #fff;
        font-weight: bold;
        font-size: 1.5em;
    }
    .member{
        position:absolute;
        bottom:5%;
        width: 85%;
        height: 5%;
        text-align: right;
        color:white;
        display: inline;
    }
</style>


