<template>
    <div id="wrapper" class="addGoods">
        <div class="header">
            <div class="prev" v-on:click="cancel"></div>
            <div class="htitle">상품추가</div>
        </div>
        <div class="condition">
            <div class="firstCondition">
                <input type="text" v-model="gName" placeholder="제조사, 상품명 검색"> 
                <button v-on:click="getitemList">검색</button>
            </div>
            <div class="secondCondition">
                <select>
                    <option value="">카테고리 선택</option>
                    <option value="">1</option>
                    <option value="">2</option>
                    <option value="">3</option>
                    <option value="">4</option>
                </select>
            </div>
        </div>
        
        <div class="itemList">
            <div class="title">
                <span style="font-weight:bold;"> 상품목록</span>
            </div>
            <div class="items">
                <div class="goodsItem" v-for="(item, index) in itemList" v-bind:key="index">
                    <div class="chk">
                        <input type="checkbox" v-bind:id="index" v-model="item.check" true-value="true" false-alue="false" v-on:change="cnt(item.check)">
                        <label v-bind:for="index"></label>
                    </div>
                    <div class="imgWrap">
                        <img src="data:image/png;base64," alt="img">
                    </div>
                    <div class="info">
                        [{{item.manufacturer}}]{{item.itemName}}
                         <br>
                         <span style="font-size:0.8em;color:gray;">{{item.unit}} - {{item.origin}}</span>
                    </div>
                    <div class="price">
                        {{item.price1}}
                    </div>
                </div>
            </div>
        </div>
        <div class="footer">
            <div class="qty">
                <span style="padding-left:2.5%;font-weight:bold;">선택상품</span>
                <span style="float:right;padding-right:2.5%;font-weight:bold;">{{this.qty}}</span>
            </div>
            <div class="buttons">
                <button v-on:click="cancelAdd" class="cancel">추가 취소</button>
                <button v-on:click="addOrderList" class="submit">상품추가하기</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'AddGoods',
        data () {
            return {
                gName: '',
                prevSearch: '',
                qty: 0,
                itemList: [],
            }
        },
        methods: {
            getitemList() {
                this.$http.get('http://192.168.64.131:8080/app/item/search?keyword=' + this.gName)
                .then((response) => {
                    console.log(response.data);
                    this.itemList = response.data;
                    for(var i=0; i < this.itemList.length ; i++){
                        this.itemList[i].check = "false";
                        this.itemList[i].qty = 1;
                    }
                    this.prevSearch = this.gName;
                    this.gName = ""
                })
                .catch((err) => {
                    console.log(err)
                })
            },
            addOrderList() {
                for(var i=0; i<this.itemList.length;i++){
                    if(this.itemList[i].check === "true"){
                        let test = this.$store.state.orderList.findIndex((item) => {
                            return item.id === this.itemList[i].id;
                        })
                        console.log(test)
                        if(test !== -1){
                            continue;
                        }
                        console.log(this.itemList[i].itemName)
                        this.$store.state.orderList.push(this.itemList[i]);
                        this.$router.replace('order')
                    }
                }
                console.log(this.$store.state.orderList)
            },
            cancelAdd() {
                this.$router.replace('addGoods');
                this.getitemList();
            },
            cancel () {
                this.$router.replace('/order');
            },
            auth() {
                if(!this.$store.state.isAuth){
                    this.$router.replace('/login');
                }
            },
            cnt(item) {
                if(item){
                    this.qty+=1;
                }else{
                    this.qty-=1;
                }
                
            }
        },
        created() {
            this.auth();
            this.getitemList();
        }
    }
</script>

<style>
    .addGoods{
        background-color: rgba(248,248,248);
    }
    .addGoods .header{
        width:100%;
        height:5%;
        color:white;
        background-image: url('../assets/HeaderBar.png');
        background-repeat: no-repeat;
        background-size: cover;
        
    }
    .addGoods .header .prev{
        width:10%;
        height:100%;
        float: left;
        background-image: url('../assets/Prev.png');
        background-size:contain;
        background-position: center center;
        background-repeat: no-repeat;
    }
    .addGoods .header .htitle{
        width: 90%;
        height:100%;
        font-weight: bold;
        font-size: 1.5em;
        text-align: center;
    }
    .addGoods .condition{
        width:90%;
        height:15%;
        margin:2.5% auto;
    }
    .addGoods .condition input{
        vertical-align: middle;
        width: 80%;
        height: 80%;
        font-size: 1.2em;
    }
    .addGoods .condition button{
        width: 15%;
        vertical-align:bottom;
        height:80%;
        background: white;
        color:rgba(43,154,131);
        font-size:1.2em;
    }
    .addGoods .condition .firstCondition, .addGoods .condition .secondCondition {
        width: 100%;
        height: 45%;
        margin: 2% 0;
        background: white;
    }
    .addGoods .condition .secondCondition select{
        width: 100%;
        height: 100%;
        font-size: 1.2em;
        color:gray;
    }
    .addGoods .itemList{
        width: 100%;
        height: 60%;
        background: white;
    }
    .addGoods .itemList .title {
        width: 100%;
        height: 5%;
        border-bottom:0.5px solid lightgray;
        margin: 2% 0;
        padding-left: 2.5%;
    }
    .addGoods .items{
        width: 100%;
        height: 95%;
        overflow: auto;
    }
    .addGoods .items .goodsItem{
        width: 95%;
        height: 20%;
        margin: 0 auto;
        display:flex;
        align-items: center;
        justify-content: center;
        border-top:0.1px solid lightgray;
        border-bottom: 0.1px solid lightgray;
    }
    .addGoods .items .goodsItem .imgWrap{
        width: 30%;
        height: 30%;
        overflow:hidden;
        border: 0.5px solid lightgray;
    }
    .addGoods .items .goodsItem .chk{
        width: 5%;
        height: 100%;
    }
    .addGoods .items .goodsItem .chk input{
        width:100%;
        height:100%;
    }
    .addGoods .items .goodsItem .imgWrap img {
        width: 100%;
        height:100%;
    }
    .addGoods .items .goodsItem .info{
        width: 40%;
    }
    .addGoods .items .goodsItem .price{
        width: 30%;
        text-align: right;
        font-size: 1.2em;
        font-weight: bold;
    }
    .addGoods .footer{
        width:100%;
        height:17%;
    }
    .addGoods .footer .qty{
        width: 100%;
        height: 50%;
        margin: 0 auto;
    }
    .addGoods .footer .buttons{
        width: 100%;
        height: 50%;
        text-align: center;
    }
    .addGoods .footer .buttons .cancel{
        width: 40%;
        height: 90%;
        background: rgba(248,248,248);
        border: 0.1px solid gray;
        font-weight: bold;
    }
    .addGoods .footer .buttons .submit{
        width: 40%;
        height: 90%;
        background: rgba(29,151,122);
        color:white;
        font-weight: bold;
    }
</style>

<style>
    
</style>


