<template>
    <div>
        <ul id="categories">
            <li class="pusher"></li>
        </ul>
        <ul id="categories">
            <li>
                <div style="background:#f00">
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "Test"
    }
</script>

<style>
#categories{
  width:90%;
  position: absolute;
}
#categories li{
  position: relative;;
  list-style-type:none;
  width:40%; /* = (100-2.5) / 3.5 */
  padding-bottom: 46%; /* =  width /0.866 */
  overflow:hidden;
  transform: rotate(-60deg) skewY(30deg);
}
#categories li:nth-child(3n+2){
  margin:0 1%;
}
#categories li:nth-child(6n+4){
  margin-left:0.5%;
}
#categories li:nth-child(6n+4), #categories li:nth-child(6n+5), #categories li:nth-child(6n+6) {
margin-top: -6.9285714285%;
margin-bottom: -6.9285714285%;
transform: translateX(50%) rotate(-60deg) skewY(30deg);
}
#categories li *{
  position:absolute;
  visibility:visible;
}
#categories li > div{
  width:100%;
  height:100%;
  text-align:center;
  color:#fff;
  overflow:hidden;
  transform: skewY(-30deg) rotate(60deg);
}

/* HEX CONTENT */
#categories li img{
  left:-100%; right:-100%;
  width: auto; height:100%;
  margin:0 auto;
}

#categories div h1, #categories div p{
  width:100%;
  background-color:#008080; background-color: rgba(0, 128, 128, 0.8);
  transition: top .2s ease-out, bottom .2s ease-out, .2s padding .2s ease-out;
}
#categories li h1{
  bottom:110%;
  font-style:italic;
  font-weight:normal;
  font-size:1.5em;
  padding-top:100%;
  padding-bottom:100%;
}

</style>


