<template>
    <div id="wrapper" class="statistics">
        <div class="header">
            <div class="prev" v-on:click="cancel"></div>
            
            <div class="htitle">
                통계
            </div>
        </div>
        <div class="selectDate">
            <div class="radio">
                <input type="radio" v-model="selectDate" value="total" id="totalStatics" v-on:change="radioTest">
                <input type="radio" v-model="selectDate" value="lastMonth" id="lastMonth" v-on:change="radioTest">
                <input type="radio" v-model="selectDate" value="thisMonth" id="thisMonth" v-on:change="radioTest">
            </div>
        
            <div class="lastMonth">
                <label for="lastMonth">지난달</label>
            </div>
            <div class="thisMonth">
                <label for="thisMonth">이번달</label>
            </div>
            <div class="totalStatistics">
                <label for="totalStatics">전체</label>
            </div>
            <div class="datePicker">
                2018.07.01 ~ 2018.07.31 기간선택 >
            </div>
        </div>
        <div class="statics">
            <div class="totalOrderPrice">
                <h2>총 주문금액</h2> <h2>{{orderStatics.total_payment}}</h2>
            </div>
            <div class="totalCase">
                <h3>총 주문건수</h3> <h3>{{orderStatics.total_orderNumber}}</h3>
                <h3>총 주문수량</h3> <h3>{{orderStatics.total_quantity}}</h3>
            </div>
            <div class="orderStatics">
                <div class="orderStaticsHeader">
                    <h3>주문 상품통계</h3> <header>주문 수량순</header>
                </div>
                <div class="orderItemStatics" v-for="(item, index) in orderItemStatics" v-bind:key="index">
                    {{index + 1}} - {{item.itemName}} - {{item.unit}} - {{item.totalQuantity}} - {{item.unit}}
                </div>
            </div>
        </div>

    </div>
</template>

<script>
    var baseurl = "http://freshntech.cafe24.com";
    export default {
        name: 'Statistics',
        data () {
            return {
                selectDate: 'total',
                startDate: '',
                endDate: '',
                orderItemStatics: [],
                orderStatics: {
                    'total_payment': 0,
                    'total_quantity': 0,
                    'total_orderNumber':0
                }
            }
        },
        methods: {
            auth () {
                if(!this.$store.state.isAuth){
                    this.$router.replace('/login');
                }
            },
            cancel() {
                this.$router.replace('/');
            },
            getStatistics () {
                this.$http.get(baseurl + '/order/statistics/' + this.$store.state.usercode + '?condition=' + this.selectDate)
                .then((response) => {
                    //console.log(response.data);
                    this.orderItemStatics = response.data.orderItemStatics;
                    this.orderStatics = response.data.orderStatics;
                })
                .catch((err) => {
                    console.log(err)
                })
            },
            radioTest(){
                console.log(this.selectDate);
                this.getStatistics();
            }
        },
        mounted() {
            this.auth();
            this.getStatistics();
            console.log(this.selectDate);
        }
    }
</script>
<style>
    
</style>

<style scoped>
    .statistics{
        width: 100%;
        height: 100%;
        overflow: hidden;
        background: rgba(248,248,248);
    }
    .statistics .header{
        width:100%;
        height:5%;
        color:white;
        background-image: url('../assets/HeaderBar.png');
        background-repeat: no-repeat;
        background-size: cover;
    }
    .statistics .header .cancel{
        width:10%;
        height:100%;
        float: left;
        text-align: center;
        font-size: 1.2em;
        font-weight: bold;
    }
    .statistics .header .htitle{
        width: 90%;
        height:100%;
        font-weight: bold;
        font-size: 1.5em;
        text-align: center;
    }
    .statistics .header .prev{
        width:10%;
        height:100%;
        float: left;
        background-image: url('../assets/Prev.png');
        background-size:contain;
        background-position: center center;
        background-repeat: no-repeat;
    }
    .statistics .selectDate{
        border:0.1px solid #f00;
    }
    .statistics .radio{
        display: none;
    }
</style>


