<template>
<div id="wrapper" class="refundHistory">
        <div class="refundDetail inactive">
            refundDetail
        </div>
        <div class="header">
            <div class="prev" v-on:click="cancel"></div>
            
            <div class="htitle">
                반품내역
            </div>
        </div>
        <div class="condition">
            <select name="" v-model="lastDay">
                <option value="7">지난 7일</option>
                <option value="30">지난 한달</option>
                <option value="365">지난 1년</option>
            </select>
            <select name="" v-model="refundState" v-on:change="filterHistory">
                <option value="전체">전체상태</option>
                <option value="반품완료">반품완료</option>
                <option value="반품대기">반품대기</option>
            </select>
        </div>
        <div class="historyList">
            <div class="historyItem" v-for="(item, index) in displayItem" v-bind:key="index">
                {{item.id}} - {{item.orderdate}} - {{item.amount}} - {{item.orderstate}}
            </div>
        </div>
    </div>
</template>

<script>
    var baseurl = "http://freshntech.cafe24.com";
    export default {
        name:'RefundHistory',
                data () {
            return {
                lastDay: '7',
                refundState: '전체',
                historyItem: [],
                displayItem: [],
                refundDetail: {},
            }
        },
        methods: {
            auth(){
                if(!this.$store.state.isAuth){
                    this.$router.replace('/login');
                }
            },
            cancel() {
                this.$router.replace('/');
            },
            getHistory () {
                console.log(this.$store.state.usercode);
                this.$http.get(baseurl + '/order/getRefund/' + this.$store.state.usercode + '?condition=' + this.lastDay)
                .then((response) => {
                    this.historyItem = response.data;
                    this.filterHistory();
                })
                .catch((err) => {
                    console.log(err);
                })
            },
            filterHistory () {
                if(this.historyItem.length <= 0){
                    return;
                }
                if(this.refundState ==="전체"){
                    this.displayItem = this.historyItem;
                    return;
                }
                let history;
                for(var i=0; i<this.historyItem.length; i++){
                    history = this.historyItem.filter((item) => {
                        return item.orderstate === this.refundState;
                    })
                }
                this.displayItem = history;
            }
        },
        created(){
            this.auth();
            this.getHistory();
        }
    }
</script>

<style>
    .refundDetail{
        position: absolute;
        background: rgba(0,0,0,0.4);
        width:100%;
        height:100%;
    }
    .refundHistory{
        width: 100%;
        height: 100%;
        overflow: hidden;
        background: rgba(248,248,248);
    }
    .refundHistory .header{
        width:100%;
        height:5%;
        color:white;
        background-image: url('../assets/HeaderBar.png');
        background-repeat: no-repeat;
        background-size: cover;
    }
    .refundHistory .header .cancel{
        width:10%;
        height:100%;
        float: left;
        text-align: center;
        font-size: 1.2em;
        font-weight: bold;
    }
    .refundHistory .header .htitle{
        width: 90%;
        height:100%;
        font-weight: bold;
        font-size: 1.5em;
        text-align: center;
    }
    .refundHistory .header .prev{
        width:10%;
        height:100%;
        float: left;
        background-image: url('../assets/Prev.png');
        background-size:contain;
        background-position: center center;
        background-repeat: no-repeat;
    }
    .refundHistory .condition{
        width: 95%;
        height: 5%;
        margin:2.5% auto;
        text-align: center;
    }
    .refundHistory .condition .filterDate{
        width:45%;
        height:50%;
        background: #fff;
    }
    .refundHistory .condition .filterState{
        width:45%;
        height:50%;
        background: #fff;
    }
    
    .refundHistory .historyList{
        width:100%;
        height:87%;
        overflow: auto;
    }
    .refundHistory .historyItem{
        width:100%;
        height:8%;
        background: #fff;
        margin: 1% 0;
    }
    .refundHistory .historyItem .orderInfo{
        width:70%;
        height: 100%;
        display:inline-block;
        padding-top:2%;
    }
    .refundHistory .historyItem .orderState{
        width: 25%;
        height:100%;
        display:inline-block;
        text-align: right;
    }
    .inactive{
        display:none;
    }
    .active{
        display:block;
    }
</style>


